/* CRUD.css */

/* Sidebar */
#sidebar {
    background-color: #333;
    color: #fff;
    padding: 1rem;
  }
  
  #sidebar h1 {
    font-size: 1.5rem;
    margin: 0;
  }
  
  /* CRUD Container */
  .crud {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Table */
  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1rem;
  }
  
  table td {
    padding: 0.5rem;
    border: 1px solid #ccc;
    font-size: 0.9rem;
  }
  
  table th {
    padding: 0.5rem;
    border: 1px solid #ccc;
    background-color: #f5f5f5;
    font-weight: bold;
    text-align: left;
  }
  
  table td input[type="text"] {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 0.9rem;
    text-align: center; /* Center the text */
    box-sizing: border-box; /* Include padding and border in width calculation */
  }
  
  /* Buttons */
  button {
    padding: 0.5rem 1rem;
    margin-right: 0.5rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
    text-transform: uppercase;
  }
  
  button.edit {
    background-color: #2196f3;
    color: #fff;
  }
  
  button.delete {
    background-color: #f44336;
    color: #fff;
  }
  
  button[type="submit"] {
    background-color: #4caf50;
    color: #fff;
  }
  
  /* Add Form */
  .addForm {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
  }
  
  .addForm input[type="text"] {
    width: 100%;
    max-width: 300px; /* Adjust the max-width as needed */
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 0.9rem;
    text-align: center; /* Center the text */
    box-sizing: border-box; /* Include padding and border in width calculation */
  }
  
  .addForm button[type="submit"] {
    margin-left: 0.5rem;
  }
  
  /* Error Message */
  .error {
    color: #f44336;
    margin-top: 0.5rem;
    font-size: 0.9rem;
  }
  